ul {
    margin: 5px;
    list-style-type: none;
}

.menubar{
    text-align: left;
}
.menubar ul li {
    text-decoration-style: none;
    display: inline;
    line-height: 30px;
    font-size: 25px;
    padding: 0px 5px;
    margin: 5px 5px;
    background-color: cadetblue;
    border-radius: 10px;
    text-align:left;
}

.menubar ul li:hover {
    background-color : green;
}

ul a {
    color: white;
    padding: 5px;
}

a:hover{
    color: white;
}

